export default "<template>\r\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  const defaultEchdata = {\r\n    xData: ['1月', '2月', '3月', '4月', '5月', '6月'],\r\n    yData: [820, 732, 701, 634, 590, 330, 220]\r\n  };\r\n  export default {\r\n    props: {\r\n      echdata: {\r\n        type: [Object, Array],\r\n        default: () => defaultEchdata\r\n      },\r\n      titleText: {\r\n        type: String,\r\n        default: '标题'\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`,\r\n        chart: null\r\n      };\r\n    },\r\n    mounted() {\r\n      this.$nextTick(() => {\r\n        this.initEchart();\r\n      });\r\n    },\r\n    methods: {\r\n      initEchart() {\r\n        let echartEle = echarts.init(document.getElementById(this.eleId));\r\n        const option = {\r\n          title: {\r\n            text: this.titleText,\r\n            // subtext: '2023年6月8日',\r\n            // left: 'center',\r\n            textStyle: {\r\n              color: '#fff', // 字体颜色 备用色  '#2B9DF2'\r\n              fontSize: '14px', // 字体大小\r\n              fontWeight: 600 // 字体粗细\r\n            }\r\n          },\r\n          tooltip: {\r\n            trigger: 'axis',\r\n            axisPointer: {\r\n              // 坐标轴指示器，坐标轴触发有效\r\n              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'\r\n            }\r\n          },\r\n          grid: {\r\n            left: '0',\r\n            right: '10',\r\n            top: '50',\r\n            bottom: '0',\r\n            containLabel: true\r\n          },\r\n          xAxis: [\r\n            {\r\n              type: 'category',\r\n              data: this.echdata.xData,\r\n              axisLabel: {\r\n                interval: 0,\r\n                fontSize: 12,\r\n                margin: 10,\r\n                textStyle: {\r\n                  color: '#96C2FF'\r\n                }\r\n              },\r\n              axisLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  color: '#2B9DF2'\r\n                }\r\n              }\r\n            }\r\n          ],\r\n          yAxis: [\r\n            {\r\n              type: 'value',\r\n              name: '(万人)',\r\n              min: 0,\r\n              max: 1000,\r\n              splitNumber: 6,\r\n              nameLocation: 'end',\r\n              nameTextStyle: {\r\n                fontSize: 10,\r\n                color: '#FDFEFF',\r\n                padding: [0, 0, 0, -40]\r\n              },\r\n              splitLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  type: 'dashed', // solid,dashed,dotted\r\n                  width: 1,\r\n                  color: '#11366e',\r\n                  opacity: 0.8\r\n                }\r\n              },\r\n              axisLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  color: '#2B9DF2'\r\n                }\r\n              },\r\n              axisTick: {\r\n                show: false\r\n              },\r\n              splitArea: {\r\n                show: false\r\n              },\r\n              axisLabel: {\r\n                fontSize: 12,\r\n                textStyle: {\r\n                  color: '#96C2FF' // 坐标值得具体的颜色\r\n                }\r\n              }\r\n            }\r\n          ],\r\n          series: [\r\n            {\r\n              type: 'line',\r\n              color: '#12876c',\r\n              symbolSize: 4,\r\n              smooth: true, // 是否平滑\r\n              label: {\r\n                show: true,\r\n                fontSize: 10,\r\n                color: '#e7e626ff',\r\n                position: 'top'\r\n              },\r\n              lineStyle: {\r\n                width: 1,\r\n                type: 'solid'\r\n              },\r\n              areaStyle: {\r\n                color: {\r\n                  type: 'linear',\r\n                  x: 0,\r\n                  y: 0,\r\n                  x2: 0,\r\n                  y2: 1,\r\n                  colorStops: [\r\n                    { offset: 0, color: 'rgba(2, 251, 189, .2)' },\r\n                    { offset: 1, color: 'rgba(2, 251, 189, 0)' }\r\n                  ],\r\n                  globalCoord: false\r\n                }\r\n              },\r\n              data: this.echdata.yData\r\n            }\r\n          ]\r\n        };\r\n        echartEle.setOption(option);\r\n        // 实时缩放\r\n        window.addEventListener('resize', () => {\r\n          echartEle.resize();\r\n        });\r\n        //\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";