export default "<template>\r\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  //tag1-start\r\n  const defaultEchdata = {\r\n    xData: ['第一季', '第二季', '第三季', '第四季'],\r\n    yData: [50, 20, 40, 35]\r\n  };\r\n  //tag1-end\r\n  //tag1B-start\r\n  const defaultTitle = '标题';\r\n  //tag1B-end\r\n  export default {\r\n    props: {\r\n      echdata: {\r\n        type: [Object, Array],\r\n        default: () => defaultEchdata\r\n      },\r\n      titleText: {\r\n        type: String,\r\n        default: defaultTitle\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`,\r\n        chart: null\r\n      };\r\n    },\r\n    mounted() {\r\n      this.$nextTick(() => {\r\n        this.initEchart();\r\n      });\r\n    },\r\n    methods: {\r\n      initEchart() {\r\n        let echartEle = echarts.init(document.getElementById(this.eleId));\r\n        var option = {\r\n          legend: {\r\n            // 图例组件，颜色和名字\r\n            top: 0,\r\n            right: 0,\r\n            textStyle: {\r\n              color: 'rgba(255, 255, 255, .5)',\r\n              fontSize: 14\r\n            },\r\n            // icon: 'roundRect',\r\n            itemWidth: 16,\r\n            itemHeight: 10,\r\n            itemGap: 5\r\n          },\r\n          tooltip: {\r\n            trigger: 'axis',\r\n            backgroundColor: 'rgba(0, 0, 0, .8)',\r\n            borderColor: 'rgba(0, 0, 0, .8)',\r\n            textStyle: {\r\n              color: '#fff',\r\n              fontSize: 18\r\n            },\r\n            axisPointer: {\r\n              // 坐标轴指示器，坐标轴触发有效\r\n              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'\r\n            }\r\n          },\r\n          grid: [\r\n            {\r\n              left: '3%',\r\n              right: '3%',\r\n              bottom: '0',\r\n              top: '20%',\r\n              containLabel: true\r\n            }\r\n          ],\r\n          xAxis: {\r\n            type: 'category',\r\n            axisLabel: {\r\n              color: 'rgba(255, 255, 255, .7)',\r\n              fontSize: 14,\r\n              interval: 0\r\n            },\r\n            axisLine: {\r\n              lineStyle: {\r\n                color: 'rgba(209, 209, 209, .3)'\r\n              }\r\n            },\r\n            data: this.echdata.xData\r\n          },\r\n          yAxis: [\r\n            {\r\n              type: 'value',\r\n              name: '',\r\n              axisLabel: {\r\n                color: 'rgba(255, 255, 255, .7)',\r\n                fontSize: 14\r\n              },\r\n              axisLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  color: 'rgba(209, 209, 209, .3)'\r\n                }\r\n              },\r\n              splitLine: {\r\n                show: false\r\n              }\r\n            }\r\n          ],\r\n          series: [\r\n            {\r\n              name: '参保人数(万人)',\r\n              type: 'line',\r\n              smooth: true,\r\n              showAllSymbol: true,\r\n              symbol: 'circle',\r\n              symbolSize: 4,\r\n              color: '#0ED793',\r\n              itemStyle: {\r\n                normal: {\r\n                  color: '#040B2B',\r\n                  borderColor: '#0ED793',\r\n                  borderWidth: 1\r\n                }\r\n              },\r\n              lineStyle: {\r\n                width: 1,\r\n                color: '#0ED793'\r\n                // type: 'dashed'\r\n              },\r\n              areaStyle: {\r\n                color: {\r\n                  type: 'linear',\r\n                  x: 0,\r\n                  y: 0,\r\n                  x2: 0,\r\n                  y2: 1,\r\n                  colorStops: [\r\n                    { offset: 0, color: 'rgba(2, 251, 189, .2)' },\r\n                    { offset: 1, color: 'rgba(2, 251, 189, 0)' }\r\n                  ],\r\n                  globalCoord: false\r\n                }\r\n              },\r\n              label: {\r\n                show: true,\r\n                fontSize: 14,\r\n                color: '#fff',\r\n                position: 'top'\r\n              },\r\n              data: this.echdata.yData\r\n            }\r\n          ]\r\n        };\r\n        echartEle.setOption(option);\r\n        // 实时缩放\r\n        window.addEventListener('resize', () => {\r\n          echartEle.resize();\r\n        });\r\n        //\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";