export default "<template>\r\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  //tag1-start\r\n  const defaultEchdata = {\r\n    xData: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],\r\n    yData: [150, 230, 224, 218, 135, 147, 260]\r\n  };\r\n  //tag1-end\r\n  //tag1B-start\r\n  const defaultTitle = '标题';\r\n  //tag1B-end\r\n  export default {\r\n    props: {\r\n      echdata: {\r\n        type: [Object, Array],\r\n        default: () => defaultEchdata\r\n      },\r\n      titleText: {\r\n        type: String,\r\n        default: defaultTitle\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`,\r\n        chart: null\r\n      };\r\n    },\r\n    mounted() {\r\n      this.$nextTick(() => {\r\n        this.initEchart();\r\n      });\r\n    },\r\n    methods: {\r\n      initEchart() {\r\n        let echartEle = echarts.init(document.getElementById(this.eleId));\r\n        var option = {\r\n          title: {\r\n            text: '折线图', // 标题文本\r\n            left: 'center', // 标题位置居中\r\n            textStyle: {\r\n              // 标题字体样式\r\n              fontWeight: 'bold',\r\n              fontSize: 20\r\n            }\r\n          },\r\n          tooltip: {\r\n            trigger: 'axis', // 鼠标悬浮提示类型为折线图\r\n            axisPointer: {\r\n              // 坐标轴指示器配置\r\n              type: 'line', // 指示器类型为线\r\n              lineStyle: {\r\n                color: '#ccc'\r\n              },\r\n              label: {\r\n                backgroundColor: '#ccc'\r\n              }\r\n            }\r\n          },\r\n          legend: {\r\n            data: ['销量'], // 图例为“销量”\r\n            textStyle: {\r\n              // 图例字体样式\r\n              fontSize: 16\r\n            }\r\n          },\r\n          xAxis: {\r\n            name: '日期', // X轴名称\r\n            type: 'category', // X轴类型为类别型\r\n            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'], // X轴数据\r\n            axisLine: {\r\n              // X轴轴线样式\r\n              lineStyle: {\r\n                color: '#999'\r\n              }\r\n            },\r\n            axisLabel: {\r\n              // X轴刻度标签样式\r\n              fontSize: 14,\r\n              color: '#666'\r\n            }\r\n          },\r\n          yAxis: {\r\n            name: '销量（万）', // Y轴名称\r\n            type: 'value', // Y轴类型为数值型\r\n            axisLine: {\r\n              // Y轴轴线样式\r\n              lineStyle: {\r\n                color: '#999'\r\n              }\r\n            },\r\n            axisLabel: {\r\n              // Y轴刻度标签样式\r\n              fontSize: 14,\r\n              color: '#666'\r\n            },\r\n            splitLine: {\r\n              // 分隔线样式\r\n              lineStyle: {\r\n                color: ['#ccc'],\r\n                type: 'dashed'\r\n              }\r\n            }\r\n          },\r\n          series: [\r\n            {\r\n              name: '销量', // 数据系列名称\r\n              type: 'line', // 图表类型为折线图\r\n              data: [5, 20, 36, 10, 10, 20, 15], // 数据\r\n              symbolSize: 10, // 数据节点大小\r\n              itemStyle: {\r\n                // 数据节点样式\r\n                color: '#0077FF'\r\n              },\r\n              lineStyle: {\r\n                // 折线样式\r\n                color: '#0077FF',\r\n                width: 2\r\n              },\r\n              areaStyle: {\r\n                // 区域填充样式\r\n                color: {\r\n                  type: 'linear',\r\n                  x: 0,\r\n                  y: 0,\r\n                  x2: 0,\r\n                  y2: 1,\r\n                  colorStops: [\r\n                    {\r\n                      offset: 0,\r\n                      color: 'rgba(0, 119, 255, 0.5)'\r\n                    },\r\n                    {\r\n                      offset: 1,\r\n                      color: 'rgba(255, 255, 255, 0.5)'\r\n                    }\r\n                  ]\r\n                }\r\n              }\r\n            }\r\n          ]\r\n        };\r\n        echartEle.setOption(option);\r\n        // 实时缩放\r\n        window.addEventListener('resize', () => {\r\n          echartEle.resize();\r\n        });\r\n        //\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";