export default "<template>\r\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  //tag1-start\r\n  const defaultEchdata = {\r\n    xData: ['2015', '2016', '2017', '2018', '2019', '2020'],\r\n    yData: [5, 20, 36, 10, 10, 20],\r\n    yData2: [15, 30, 15, 35, 20, 25]\r\n  };\r\n  //tag1-end\r\n  //tag1B-start\r\n  const defaultTitle = '标题';\r\n  //tag1B-end\r\n  export default {\r\n    props: {\r\n      echdata: {\r\n        type: [Object, Array],\r\n        default: () => defaultEchdata\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`,\r\n        chart: null\r\n      };\r\n    },\r\n    mounted() {\r\n      this.$nextTick(() => {\r\n        this.initEchart();\r\n      });\r\n    },\r\n    methods: {\r\n      initEchart() {\r\n        let echartEle = echarts.init(document.getElementById(this.eleId));\r\n        // 配置项\r\n        var option = {\r\n          // 标题\r\n          title: {\r\n            text: '折线图', // 主标题文本\r\n            subtext: '示例', // 副标题文本\r\n            left: 'center' // 标题位置\r\n          },\r\n\r\n          // 图例\r\n          legend: {\r\n            data: ['数据一', '数据二'], // 图例数据\r\n            top: '5%' // 图例位置\r\n          },\r\n\r\n          // 提示框\r\n          tooltip: {\r\n            trigger: 'axis'\r\n          },\r\n\r\n          // x 轴\r\n          xAxis: {\r\n            type: 'category', // 类目轴\r\n            data: this.echdata.xData, // x 轴数据\r\n            axisLine: {\r\n              // x 轴轴线设置\r\n              lineStyle: {\r\n                color: '#999' // 灰色\r\n              }\r\n            },\r\n            axisTick: {\r\n              // x 轴刻度线设置\r\n              show: false // 不显示刻度线\r\n            }\r\n          },\r\n\r\n          // y 轴\r\n          yAxis: {\r\n            type: 'value', // 数值轴\r\n            axisLine: {\r\n              // y 轴轴线设置\r\n              show: false // 不显示轴线\r\n            },\r\n            axisTick: {\r\n              // y 轴刻度线设置\r\n              show: false // 不显示刻度线\r\n            }\r\n          },\r\n\r\n          // 数据系列\r\n          series: [\r\n            {\r\n              name: '数据一',\r\n              type: 'line',\r\n              smooth: true, // 平滑曲线\r\n              symbol: 'circle', // 折线拐点形状\r\n              symbolSize: 8, // 折线拐点大小\r\n              lineStyle: {\r\n                // 折线颜色和线条样式设置\r\n                color: '#61a0a8',\r\n                width: 2 // 折线线宽度\r\n              },\r\n              itemStyle: {\r\n                // 折线拐点样式设置\r\n                color: '#c23531'\r\n              },\r\n              areaStyle: {\r\n                // 折线下方填充区域\r\n                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [\r\n                  {\r\n                    offset: 0,\r\n                    color: '#fff'\r\n                  },\r\n                  {\r\n                    offset: 1,\r\n                    color: '#61a0a8'\r\n                  }\r\n                ]),\r\n                opacity: 0.2\r\n              },\r\n              data: this.echdata.yData\r\n            },\r\n            {\r\n              name: '数据二',\r\n              type: 'line',\r\n              smooth: true, // 平滑曲线\r\n              symbol: 'circle', // 折线拐点形状\r\n              symbolSize: 8, // 折线拐点大小\r\n              lineStyle: {\r\n                // 折线颜色和线条样式设置\r\n                color: '#d48265',\r\n                width: 2 // 折线线宽度\r\n              },\r\n              itemStyle: {\r\n                // 折线拐点样式设置\r\n                color: '#91c7ae'\r\n              },\r\n              areaStyle: {\r\n                // 折线下方填充区域\r\n                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [\r\n                  {\r\n                    offset: 0,\r\n                    color: '#fff'\r\n                  },\r\n                  {\r\n                    offset: 1,\r\n                    color: '#d48265'\r\n                  }\r\n                ]),\r\n                opacity: 0.2\r\n              },\r\n              data: this.echdata.yData2\r\n            }\r\n          ],\r\n\r\n          // 工具箱\r\n          toolbox: {\r\n            feature: {\r\n              dataView: {\r\n                // 数据视图\r\n                show: true, // 显示数据视图\r\n                readOnly: false // 可编辑\r\n              },\r\n              saveAsImage: {\r\n                // 保存为图片\r\n                show: true // 显示保存为图片\r\n              },\r\n              restore: {\r\n                // 重置\r\n                show: true // 显示重置\r\n              },\r\n              dataZoom: {\r\n                //// 数据区域缩放\r\n                show: true // 显示数据区域缩放\r\n              },\r\n              magicType: {\r\n                // 图表类型切换\r\n                show: true, // 显示类型切换\r\n                type: ['line', 'bar'] // 可切换的图表类型\r\n              },\r\n              brush: {\r\n                // 选框组件，用于区域选择\r\n                show: true // 显示选框组件\r\n              }\r\n            },\r\n            // 工具箱总体设置\r\n            top: '5%', // 工具箱位置\r\n            right: '5%', // 工具箱位置\r\n            orient: 'vertical', // 工具箱图标布局，垂直排布\r\n            itemGap: 15, // 工具箱中各个图标之间的距离\r\n            emphasis: {\r\n              // 工具箱图标高亮样式\r\n              iconStyle: {\r\n                borderColor: '#fff',\r\n                shadowBlur: 10,\r\n                shadowOffsetX: 0,\r\n                shadowOffsetY: 0,\r\n                shadowColor: 'rgba(0, 0, 0, 0.5)'\r\n              }\r\n            }\r\n          }\r\n        };\r\n        echartEle.setOption(option);\r\n        // 实时缩放\r\n        window.addEventListener('resize', () => {\r\n          echartEle.resize();\r\n        });\r\n        //\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";