export default "<template>\r\n  <div :id=\"eleId\" class=\"echart-unit\" />\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  //tag1-start\r\n  const defaultEchdata = {\r\n    xData: ['3月', '4月', '5月', '6月', '7月', '8月'],\r\n    yData1: [31865, 24684, 19010, 22136, 18684, 7130],\r\n    yData2: [11934, 12158, 10809, 13562, 13035, 5019],\r\n    yData3: [917, 543, 340, 102, 187, 32]\r\n  };\r\n  //tag1-end\r\n  //tag1B-start\r\n  const defaultTitle = '标题';\r\n  //tag1B-end\r\n  export default {\r\n    name: '',\r\n    components: {},\r\n    props: {\r\n      echdata: {\r\n        type: Object,\r\n        default: () => defaultEchdata\r\n      },\r\n      titleText: {\r\n        type: String,\r\n        default: defaultTitle\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `echartsLine${Number(\r\n          Math.random()\r\n            .toString()\r\n            .substr(3, 3) + Date.now()\r\n        ).toString(36)}`\r\n      };\r\n    },\r\n    watch: {\r\n      echdata: {\r\n        handler: function() {\r\n          this.initEchart();\r\n        },\r\n        deep: true\r\n      }\r\n    },\r\n    created() {},\r\n    mounted() {\r\n      this.$echarts = echarts;\r\n      this.$nextTick(function() {\r\n        this.initEchart();\r\n      });\r\n    },\r\n    methods: {\r\n      initEchart() {\r\n        var option = {\r\n          title: {\r\n            text: this.titleText,\r\n            top: -2,\r\n            textStyle: {\r\n              color: '#fff', // 字体颜色\r\n              fontSize: '14px', // 字体大小\r\n              fontWeight: 600 // 字体粗细\r\n            }\r\n          },\r\n          legend: {\r\n            // 图例组件，颜色和名字\r\n            top: 0,\r\n            right: 0,\r\n            textStyle: {\r\n              color: 'rgba(255, 255, 255, .5)',\r\n              fontSize: 12\r\n            },\r\n            icon: 'roundRect',\r\n            itemWidth: 16,\r\n            itemHeight: 10,\r\n            itemGap: 5\r\n          },\r\n          tooltip: {\r\n            trigger: 'axis',\r\n            backgroundColor: 'rgba(0, 0, 0, .8)',\r\n            borderColor: 'rgba(0, 0, 0, .8)',\r\n            textStyle: {\r\n              color: '#fff',\r\n              fontSize: 18\r\n            },\r\n            axisPointer: {\r\n              // 坐标轴指示器，坐标轴触发有效\r\n              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'\r\n            }\r\n          },\r\n          grid: [\r\n            {\r\n              left: '3%',\r\n              right: '3%',\r\n              bottom: '10',\r\n              top: '40',\r\n              containLabel: true\r\n            }\r\n          ],\r\n          xAxis: {\r\n            type: 'category',\r\n            axisLabel: {\r\n              // color: 'rgba(255, 255, 255, .7)',\r\n              color: '#fff', // 字体颜色\r\n              fontSize: 13,\r\n              interval: 0\r\n            },\r\n            axisLine: {\r\n              lineStyle: {\r\n                color: 'rgba(209, 209, 209, .3)'\r\n              }\r\n            },\r\n            data: this.echdata.xData\r\n          },\r\n          yAxis: [\r\n            {\r\n              type: 'value',\r\n              name: '',\r\n              axisLabel: {\r\n                // color: 'rgba(255, 255, 255, .7)',\r\n                color: '#fff', // 字体颜色\r\n                fontSize: 13,\r\n                fontWeight: 400\r\n              },\r\n              axisLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  color: 'rgba(209, 209, 209, .3)'\r\n                }\r\n              },\r\n              splitLine: {\r\n                show: false\r\n              }\r\n            }\r\n          ],\r\n          series: [\r\n            {\r\n              name: '岗位推荐人次数',\r\n              type: 'line',\r\n              smooth: true,\r\n              showAllSymbol: true,\r\n              symbol: 'circle',\r\n              symbolSize: 8,\r\n              color: '#10D6FE',\r\n              lineStyle: {\r\n                width: 1\r\n                // type: 'dashed'\r\n              },\r\n              label: { show: true },\r\n              areaStyle: {\r\n                color: {\r\n                  type: 'linear',\r\n                  x: 0,\r\n                  y: 0,\r\n                  x2: 0,\r\n                  y2: 1,\r\n                  colorStops: [\r\n                    { offset: 0, color: 'rgba(2, 251, 189, .2)' },\r\n                    { offset: 1, color: 'rgba(2, 251, 189, 0)' }\r\n                  ],\r\n                  globalCoord: false\r\n                }\r\n              },\r\n              itemStyle: {\r\n                normal: {\r\n                  label: {\r\n                    show: true,\r\n                    textStyle: {\r\n                      fontSize: 13,\r\n                      fontWeight: 600\r\n                    }\r\n                  }\r\n                }\r\n              },\r\n              data: this.echdata.yData1\r\n            },\r\n            {\r\n              name: '职业指导人次数',\r\n              type: 'line',\r\n              smooth: true,\r\n              showAllSymbol: true,\r\n              symbol: 'circle',\r\n              symbolSize: 8,\r\n              color: '#E7E626',\r\n              lineStyle: {\r\n                width: 1\r\n                // type: 'dashed'\r\n              },\r\n              label: { show: true },\r\n              areaStyle: {\r\n                color: {\r\n                  type: 'linear',\r\n                  x: 0,\r\n                  y: 0,\r\n                  x2: 0,\r\n                  y2: 1,\r\n                  colorStops: [\r\n                    { offset: 0, color: 'rgba(254, 204, 62, .2)' },\r\n                    { offset: 1, color: 'rgba(254, 204, 62, 0)' }\r\n                  ],\r\n                  globalCoord: false\r\n                }\r\n              },\r\n              itemStyle: {\r\n                normal: {\r\n                  label: {\r\n                    show: true,\r\n                    textStyle: {\r\n                      fontSize: 13,\r\n                      fontWeight: 600\r\n                    }\r\n                  }\r\n                }\r\n              },\r\n              data: this.echdata.yData2\r\n            },\r\n            {\r\n              name: '职业培训人次数',\r\n              type: 'line',\r\n              smooth: true,\r\n              showAllSymbol: true,\r\n              symbol: 'circle',\r\n              symbolSize: 8,\r\n              // color: '#5470c6',\r\n              color: '#9599e2',\r\n              lineStyle: {\r\n                width: 1\r\n                // type: 'dashed'\r\n              },\r\n              label: { show: true },\r\n              areaStyle: {\r\n                color: {\r\n                  type: 'linear',\r\n                  x: 0,\r\n                  y: 0,\r\n                  x2: 0,\r\n                  y2: 1,\r\n                  colorStops: [\r\n                    { offset: 0, color: 'rgba(2, 21, 189, .2)' },\r\n                    { offset: 1, color: 'rgba(2, 21, 189, 0)' }\r\n                  ],\r\n                  globalCoord: false\r\n                }\r\n              },\r\n              itemStyle: {\r\n                normal: {\r\n                  label: {\r\n                    show: true,\r\n                    textStyle: {\r\n                      fontSize: 13,\r\n                      fontWeight: 600\r\n                    }\r\n                  }\r\n                }\r\n              },\r\n              data: this.echdata.yData3\r\n            }\r\n          ]\r\n        };\r\n        //\r\n        let echartEle = echarts.init(document.getElementById(this.eleId));\r\n        echartEle.setOption(option, true);\r\n        // 实时缩放\r\n        window.addEventListener('resize', () => {\r\n          echartEle.resize();\r\n        });\r\n        //\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";