export default "<template>\r\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  //tag1-start\r\n  const defaultEchdata = {\r\n    xData: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],\r\n    yData: [150, 230, 224, 218, 135, 147, 260],\r\n    yData2: [130, 200, 182, 190, 120, 127, 160]\r\n  };\r\n  //tag1-end\r\n  export default {\r\n    props: {\r\n      echdata: {\r\n        type: [Object, Array],\r\n        default: () => defaultEchdata\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`,\r\n        chart: null\r\n      };\r\n    },\r\n    watch: {\r\n      echdata: {\r\n        handler: function() {\r\n          this.dram();\r\n        },\r\n        deep: true\r\n      }\r\n    },\r\n    mounted() {\r\n      this.$nextTick(() => {\r\n        this.initEchart();\r\n      });\r\n    },\r\n    methods: {\r\n      initEchart() {\r\n        let echartEle = echarts.init(document.getElementById(this.eleId));\r\n        const option = {\r\n          grid: {\r\n            top: 30,\r\n            bottom: 0,\r\n            left: 0,\r\n            containLabel: true\r\n          },\r\n          xAxis: {\r\n            type: 'category',\r\n            data: this.echdata.xData\r\n          },\r\n          yAxis: {\r\n            type: 'value'\r\n          },\r\n          series: [\r\n            {\r\n              data: this.echdata.yData,\r\n              type: 'line'\r\n            },\r\n            {\r\n              data: this.echdata.yData2,\r\n              type: 'line' // 在上方显示\r\n            }\r\n          ]\r\n        };\r\n        echartEle.setOption(option);\r\n        // 实时缩放\r\n        window.addEventListener('resize', () => {\r\n          echartEle.resize();\r\n        });\r\n        //\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";