export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  const defaultEchdata = {\n    xData: ['2020年', '2021年', '2022年', '2023年', '2024年'],\n    yData: [100, 220, 300, 450, 500]\n  };\n  const defaultSeriesName = ['系列1', '系列2'];\n  export default {\n    props: {\n      echdata: {\n        type: [Object, Array],\n        default: () => defaultEchdata\n      },\n      titleText: {\n        type: String,\n        default: '标题'\n      },\n      seriesname: {\n        type: [Object, Array],\n        default: () => defaultSeriesName\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`,\n        chart: null\n      };\n    },\n    watch: {\n      echdata: {\n        handler(newV, oldV) {\n          this.initEchart();\n        }\n      }\n    },\n    mounted() {\n      this.$nextTick(() => {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        var imgArr = [\n          'image://data:image/png;base64,',\n          'image://data:image/png;base64,',\n          'image://data:image/png;base64,',\n          'image://data:image/png;base64,'\n        ];\n        const option = {\n          backgroundColor: '#0e2147',\n          grid: {\n            left: '30',\n            top: '5%',\n            bottom: '5%',\n            right: '5%'\n          },\n          legend: {\n            type: 'scroll',\n            data: '来电量',\n            itemWidth: 18,\n            itemHeight: 12,\n            textStyle: {\n              color: '#00ffff',\n              fontSize: 14\n            }\n          },\n          yAxis: [\n            {\n              type: 'value',\n              position: 'left',\n              nameTextStyle: {\n                color: '#00FFFF'\n              },\n              splitLine: {\n                lineStyle: {\n                  type: 'dashed',\n                  color: 'rgba(135,140,147,0.8)'\n                }\n              },\n              axisLine: {\n                show: false\n              },\n              axisTick: {\n                show: false\n              },\n              axisLabel: {\n                formatter: '{value}',\n                color: '#fff',\n                fontSize: 12\n              }\n            }\n          ],\n          xAxis: [\n            {\n              type: 'category',\n              axisTick: {\n                show: false\n              },\n              axisLine: {\n                show: false,\n                lineStyle: {\n                  color: '#0696f9'\n                }\n              },\n              axisLabel: {\n                inside: false,\n                textStyle: {\n                  color: '#fff', // x轴颜色\n                  fontWeight: 'normal',\n                  fontSize: '14',\n                  lineHeight: 22\n                }\n              },\n              data: this.echdata.xData\n            }\n          ],\n          series: [\n            {\n              //   symbolSize: 150,\n              //   symbol: imgArr[2],\n              name: '小灯光',\n              type: 'line',\n              itemStyle: {\n                normal: {\n                  borderWidth: 3,\n                  color: '#0696f9'\n                }\n              },\n              data: this.echdata.yData\n            },\n            {\n              name: '滑行的光点',\n              type: 'lines',\n              coordinateSystem: 'cartesian2d',\n              symbolSize: 30,\n              polyline: true,\n              effect: {\n                show: true,\n                trailLength: 0,\n                // symbol: 'arrow',\n                period: 10, //光点滑动速度\n                symbolSize: 150,\n                symbol: imgArr[0]\n              },\n              lineStyle: {\n                normal: {\n                  width: 1,\n                  opacity: 0.6,\n                  curveness: 0.2\n                }\n              }\n              // data: data\n            }\n          ]\n        };\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        echartEle.setOption(option);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";