export default "<template>\r\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  //tag1-start\r\n  const defaultEchdata = {\r\n    xData: ['1月', '2月', '3月', '4月', '5月', '6月'],\r\n    yData: [5, 20, 36, 10, 10, 20]\r\n  };\r\n  //tag1-end\r\n  //tag1B-start\r\n  const defaultTitle = '标题';\r\n  //tag1B-end\r\n  export default {\r\n    props: {\r\n      echdata: {\r\n        type: [Object, Array],\r\n        default: () => defaultEchdata\r\n      },\r\n      titleText: {\r\n        type: String,\r\n        default: defaultTitle\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`,\r\n        chart: null\r\n      };\r\n    },\r\n    mounted() {\r\n      this.$nextTick(() => {\r\n        this.initEchart();\r\n      });\r\n    },\r\n    methods: {\r\n      initEchart() {\r\n        let echartEle = echarts.init(document.getElementById(this.eleId));\r\n        // 折线图配置\r\n        let option = {\r\n          // 头部标题\r\n          title: {\r\n            text: 'ECharts 折线图',\r\n            textStyle: {\r\n              color: '#333', // 文字颜色\r\n              fontSize: 16, // 文字字号\r\n              fontWeight: 'bold' // 文字加粗\r\n            },\r\n            left: 'center' // 标题位置\r\n          },\r\n\r\n          // 提示框组件\r\n          tooltip: {\r\n            trigger: 'axis', // 提示框触发源，坐标轴触发\r\n            axisPointer: {\r\n              // 坐标轴指示器\r\n              type: 'cross' // 十字准星指示器\r\n            },\r\n            backgroundColor: '#fff', // 提示框背景色\r\n            borderColor: '#ccc', // 提示框边框颜色\r\n            padding: 10, // 内边距\r\n            textStyle: {\r\n              color: '#333' // 提示框文字颜色\r\n            },\r\n            extraCssText: 'box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);', // 提示框阴影样式\r\n            formatter: function(params) {\r\n              // 提示框内容\r\n              let str = `销量：${params[0].data}万`;\r\n              return str;\r\n            }\r\n          },\r\n\r\n          // 图例组件\r\n          legend: {\r\n            data: ['销量'],\r\n            textStyle: {\r\n              color: '#999' // 图例文字颜色\r\n            },\r\n            right: '2%', // 图例距离容器右侧的距离\r\n            top: '5%', // 图例距离容器顶部的距离\r\n            itemGap: 20 // 图例项之间的距离\r\n          },\r\n\r\n          // 网格组件\r\n          grid: {\r\n            left: '3%', // 网格左侧的距离\r\n            right: '4%', // 网格右侧的距离\r\n            bottom: '10%', // 网格底部的距离\r\n            containLabel: true // 包含坐标轴标签的网格区域\r\n          },\r\n\r\n          // X轴\r\n          xAxis: {\r\n            type: 'category', // 类目轴，适用于离散数据\r\n            data: this.echdata.xData,\r\n            axisTick: {\r\n              // 坐标轴刻度\r\n              show: false // 不显示坐标轴刻度\r\n            },\r\n            axisLine: {\r\n              // 坐标轴线\r\n              lineStyle: {\r\n                color: '#ccc' // 坐标轴颜色\r\n              }\r\n            },\r\n            axisLabel: {\r\n              // 坐标轴标签\r\n              color: '#666', // 坐标轴文字颜色\r\n              margin: 10 // 坐标轴文字和轴线之间的距离\r\n            }\r\n          },\r\n\r\n          // Y轴\r\n          yAxis: {\r\n            type: 'value', // 数值轴，适用于连续数据\r\n            axisTick: {\r\n              show: false // 不显示坐标轴刻度\r\n            },\r\n            axisLine: {\r\n              lineStyle: {\r\n                color: '#ccc'\r\n              }\r\n            },\r\n            axisLabel: {\r\n              color: '#666',\r\n              margin: 10\r\n            },\r\n            splitLine: {\r\n              // Y轴网格线\r\n              lineStyle: {\r\n                color: '#eee',\r\n                type: 'dashed' // 虚线\r\n              }\r\n            }\r\n          },\r\n\r\n          // 数据系列\r\n          series: [\r\n            {\r\n              name: '销量',\r\n              type: 'line',\r\n              smooth: true, // 平滑曲线\r\n              showSymbol: false, // 不显示节点图案\r\n              lineStyle: {\r\n                color: '#ffa000',\r\n                width: 2 // 折线线宽度\r\n              },\r\n              areaStyle: {\r\n                // 折线下方填充区域\r\n                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [\r\n                  {\r\n                    offset: 0,\r\n                    color: '#fff'\r\n                  },\r\n                  {\r\n                    offset: 1,\r\n                    color: '#ffa000'\r\n                  }\r\n                ]),\r\n                opacity: 0.2\r\n              },\r\n              data: this.echdata.yData\r\n            }\r\n          ],\r\n\r\n          // 工具箱\r\n          toolbox: {\r\n            feature: {\r\n              dataView: {\r\n                // 数据视图\r\n                show: true, // 显示数据视图\r\n                readOnly: false // 可编辑\r\n              },\r\n              saveAsImage: {\r\n                // 保存为图片\r\n                show: true // 显示保存为图片\r\n              },\r\n              restore: {\r\n                // 重置\r\n                show: true // 显示重置\r\n              },\r\n              magicType: {\r\n                // 图表类型转换\r\n                type: ['line', 'bar'] // 可选折线图、柱状图\r\n              }\r\n            },\r\n            right: '5%', // 工具箱距离容器右侧的距离\r\n            top: '5%' // 工具箱距离容器顶部的距离\r\n          },\r\n\r\n          // 视觉映射组件\r\n          visualMap: {\r\n            type: 'piecewise', // 分块型视觉映射组件\r\n            pieces: [\r\n              {\r\n                gt: 0,\r\n                lte: 10,\r\n                color: '#d94e5d' // 红色\r\n              },\r\n              {\r\n                gt: 10,\r\n                lte: 20,\r\n                color: '#eac736' // 黄色\r\n              },\r\n              {\r\n                gt: 20,\r\n                lte: 30,\r\n                color: '#50a3ba' // 蓝色\r\n              },\r\n              {\r\n                gt: 30,\r\n                color: '#32c5e9' // 深蓝色\r\n              }\r\n            ],\r\n            outOfRange: {\r\n              color: '#999' // 超出映射范围的颜色\r\n            },\r\n            right: '5%', // 视觉映射组件距离容器右侧的距离\r\n            bottom: '5%', // 视觉映射组件距离容器底部的距离\r\n            orient: 'horizontal' // 视觉映射组件方向设置\r\n          },\r\n\r\n          // 数据缩放组件\r\n          // dataZoom: {\r\n          //   type: 'slider', // 滑动条型数据缩放组\r\n          //   start: 0, // 数据窗口的起始位置\r\n          //   end: 100, // 数据窗口的结束位置\r\n          //   bottom: '2%', // 数据缩放组件距离容器底部的距离\r\n          //   handleIcon: 'M0,0 v9.7h5 v-9.7z', // 缩放手柄图标\r\n          //   handleSize: '80%', // 缩放手柄大小\r\n          //   handleStyle: {\r\n          //     // 缩放手柄样式\r\n          //     color: '#ddd'\r\n          //   },\r\n          //   backgroundColor: '#f7f7f7', // 滑动条背景色\r\n          //   fillerColor: '#ddd', // 滑动条填充色\r\n          //   borderColor: '#eee' // 滑动条边框颜色\r\n          // },\r\n\r\n          // 动画效果配置\r\n          animationDuration: 1000,\r\n          animationEasing: 'cubicOut'\r\n        };\r\n        // ................\r\n        echartEle.setOption(option);\r\n        // 实时缩放\r\n        window.addEventListener('resize', () => {\r\n          echartEle.resize();\r\n        });\r\n        //\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";