export default "<template>\r\n  <div :id=\"eleId\" class=\"echart-unit\" />\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n\r\n  const defaultEchdata = {\r\n    xData: [5, 75, 115, 52.65, 25.75],\r\n    yData: ['16-20岁', '21-30岁', '31-40岁', '41-50岁', '51-60岁']\r\n  };\r\n\r\n  export default {\r\n    infoTip: '备注：',\r\n    name: '',\r\n    props: {\r\n      echdata: {\r\n        type: [Object, Array],\r\n        default: () => defaultEchdata\r\n      },\r\n      titleText: {\r\n        type: String,\r\n        default: '年龄分布(单位：万人)'\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`\r\n      };\r\n    },\r\n    watch: {\r\n      echdata: {\r\n        handler: function() {\r\n          this.drainitEchartm();\r\n        },\r\n        deep: true\r\n      }\r\n    },\r\n    created() {},\r\n    mounted() {\r\n      this.$echarts = echarts;\r\n      this.$nextTick(function() {\r\n        this.initEchart();\r\n        this.onHanleClick();\r\n      });\r\n    },\r\n    methods: {\r\n      // echart 初始化\r\n      initEchart() {\r\n        const option = this.getOptions();\r\n        let echartEle = echarts.init(document.getElementById(this.eleId));\r\n        echartEle.setOption(option, true);\r\n        window.addEventListener('resize', () => {\r\n          echartEle.resize();\r\n        });\r\n      },\r\n      getOptions() {\r\n        var fontColor = '#30eee9';\r\n        const options = {\r\n          backgroundColor: '#11183c',\r\n          grid: {\r\n            left: '10px',\r\n            right: '10px',\r\n            top: '20%',\r\n            bottom: '0%',\r\n            containLabel: true\r\n          },\r\n          tooltip: {\r\n            show: true,\r\n            trigger: 'item'\r\n          },\r\n          legend: {\r\n            show: true,\r\n            x: 'center',\r\n            y: '35',\r\n            icon: 'stack',\r\n            itemWidth: 10,\r\n            itemHeight: 10,\r\n            textStyle: {\r\n              color: '#1bb4f6'\r\n            },\r\n            data: ['已采纳', '已发布', '浏览量']\r\n          },\r\n          xAxis: [\r\n            {\r\n              type: 'category',\r\n              boundaryGap: false,\r\n              axisLabel: {\r\n                color: fontColor\r\n              },\r\n              axisLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  color: '#397cbc'\r\n                }\r\n              },\r\n              axisTick: {\r\n                show: false\r\n              },\r\n              splitLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  color: '#195384'\r\n                }\r\n              },\r\n              data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']\r\n            }\r\n          ],\r\n          yAxis: [\r\n            {\r\n              type: 'value',\r\n              name: '信息量',\r\n              min: 0,\r\n              max: 1000,\r\n              axisLabel: {\r\n                formatter: '{value}',\r\n                textStyle: {\r\n                  color: '#2ad1d2'\r\n                }\r\n              },\r\n              axisLine: {\r\n                lineStyle: {\r\n                  color: '#27b4c2'\r\n                }\r\n              },\r\n              axisTick: {\r\n                show: false\r\n              },\r\n              splitLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  color: '#11366e'\r\n                }\r\n              }\r\n            },\r\n            {\r\n              type: 'value',\r\n              name: '浏览量',\r\n              min: 0,\r\n              max: 1000,\r\n              axisLabel: {\r\n                formatter: '{value} 人',\r\n                textStyle: {\r\n                  color: '#186afe'\r\n                }\r\n              },\r\n              axisLine: {\r\n                lineStyle: {\r\n                  color: '#186afe'\r\n                }\r\n              },\r\n              axisTick: {\r\n                show: false\r\n              },\r\n              splitLine: {\r\n                show: true,\r\n                lineStyle: {\r\n                  color: '#11366e'\r\n                }\r\n              }\r\n            }\r\n          ],\r\n          series: [\r\n            {\r\n              name: '已采纳',\r\n              type: 'line',\r\n              stack: '总量',\r\n              symbol: 'circle',\r\n              symbolSize: 8,\r\n              itemStyle: {\r\n                normal: {\r\n                  color: '#0092f6',\r\n                  lineStyle: {\r\n                    color: '#0092f6',\r\n                    width: 1\r\n                  },\r\n                  areaStyle: {\r\n                    //color: '#94C9EC'\r\n                    color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [\r\n                      {\r\n                        offset: 0,\r\n                        color: 'rgba(7,44,90,0.3)'\r\n                      },\r\n                      {\r\n                        offset: 1,\r\n                        color: 'rgba(0,146,246,0.9)'\r\n                      }\r\n                    ])\r\n                  }\r\n                }\r\n              },\r\n              markPoint: {\r\n                itemStyle: {\r\n                  normal: {\r\n                    color: 'red'\r\n                  }\r\n                }\r\n              },\r\n              data: [120, 132, 101, 134, 90, 230, 210, 182, 191, 234, 290, 330]\r\n            },\r\n            {\r\n              name: '已发布',\r\n              type: 'line',\r\n              stack: '总量',\r\n              symbol: 'circle',\r\n              symbolSize: 8,\r\n\r\n              itemStyle: {\r\n                normal: {\r\n                  color: '#00d4c7',\r\n                  lineStyle: {\r\n                    color: '#00d4c7',\r\n                    width: 1\r\n                  },\r\n                  areaStyle: {\r\n                    //color: '#94C9EC'\r\n                    color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [\r\n                      {\r\n                        offset: 0,\r\n                        color: 'rgba(7,44,90,0.3)'\r\n                      },\r\n                      {\r\n                        offset: 1,\r\n                        color: 'rgba(0,212,199,0.9)'\r\n                      }\r\n                    ])\r\n                  }\r\n                }\r\n              },\r\n              data: [220, 182, 191, 234, 290, 330, 310, 201, 154, 190, 330, 410]\r\n            },\r\n            {\r\n              name: '浏览量',\r\n              type: 'line',\r\n              stack: '总量',\r\n              symbol: 'circle',\r\n              symbolSize: 8,\r\n              itemStyle: {\r\n                normal: {\r\n                  color: '#aecb56',\r\n                  lineStyle: {\r\n                    color: '#aecb56',\r\n                    width: 1\r\n                  },\r\n                  areaStyle: {\r\n                    //color: '#94C9EC'\r\n                    color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [\r\n                      {\r\n                        offset: 0,\r\n                        color: 'rgba(7,44,90,0.3)'\r\n                      },\r\n                      {\r\n                        offset: 1,\r\n                        color: 'rgba(114,144,89,0.9)'\r\n                      }\r\n                    ])\r\n                  }\r\n                }\r\n              },\r\n              data: [150, 232, 201, 154, 190, 330, 410, 150, 232, 201, 154, 190]\r\n            }\r\n          ]\r\n        };\r\n        return options;\r\n      },\r\n      // 点击事件\r\n      onHanleClick() {\r\n        var echartEle = this.$echarts.init(document.getElementById(this.eleId));\r\n        echartEle.getZr().on('click', params => {\r\n          console.log('点击饿了', params);\r\n          const pointInPixel = [params.offsetX, params.offsetY];\r\n          if (echartEle.containPixel('grid', pointInPixel)) {\r\n            const xIndex = echartEle.convertFromPixel({ seriesIndex: 0 }, [params.offsetX, params.offsetY])[0];\r\n            console.log(xIndex);\r\n          }\r\n          // this.$router.push('/sy-chanyelian')\r\n          //\r\n          // var options = echartEle.getOption()\r\n          // console.log('options配置', options)\r\n        });\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";