<template>
  <div :id="eleId" class="echart-unit"></div>
</template>

<script>
  import * as echarts from 'echarts';
  const defaultEchdata = {
    xData: ['1月', '2月', '3月', '4月', '5月', '6月'],
    yData: [820, 732, 701, 634, 590, 330, 220]
  };
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: '标题'
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        let echartEle = echarts.init(document.getElementById(this.eleId));
        const option = {
          title: {
            text: this.titleText,
            // subtext: '2023年6月8日',
            // left: 'center',
            textStyle: {
              color: '#fff', // 字体颜色 备用色  '#2B9DF2'
              fontSize: '14px', // 字体大小
              fontWeight: 600 // 字体粗细
            }
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: '0',
            right: '10',
            top: '50',
            bottom: '0',
            containLabel: true
          },
          // 视觉映射组件
          visualMap: {
            type: 'piecewise', // 分块型视觉映射组件
            pieces: [
              {
                gt: 0,
                lte: 10,
                color: '#d94e5d' // 红色
              },
              {
                gt: 10,
                lte: 20,
                color: '#eac736' // 黄色
              },
              {
                gt: 20,
                lte: 30,
                color: '#50a3ba' // 蓝色
              },
              {
                gt: 30,
                color: '#32c5e9' // 深蓝色
              }
            ],
            outOfRange: {
              color: '#999' // 超出映射范围的颜色
            },
            color: '#fff',
            left: 'right', // 视觉映射组件距离容器右侧的距离
            top: 'top', // 视觉映射组件距离容器底部的距离
            orient: 'horizontal' // 视觉映射组件方向设置
          },
          xAxis: [
            {
              type: 'category',
              data: this.echdata.xData,
              axisLabel: {
                interval: 0,
                fontSize: 12,
                margin: 10,
                textStyle: {
                  color: '#96C2FF'
                }
              },
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#2B9DF2'
                }
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '(万人)',
              min: 0,
              max: 1000,
              splitNumber: 6,
              nameLocation: 'end',
              nameTextStyle: {
                fontSize: 10,
                color: '#FDFEFF',
                padding: [0, 0, 0, -40]
              },
              splitLine: {
                show: true,
                lineStyle: {
                  type: 'dashed', // solid,dashed,dotted
                  width: 1,
                  color: '#11366e',
                  opacity: 0.8
                }
              },
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#2B9DF2'
                }
              },
              axisTick: {
                show: false
              },
              splitArea: {
                show: false
              },
              axisLabel: {
                fontSize: 12,
                textStyle: {
                  color: '#96C2FF' // 坐标值得具体的颜色
                }
              }
            }
          ],
          series: [
            {
              type: 'line',
              color: '#12876c',
              symbolSize: 4,
              smooth: true, // 是否平滑
              label: {
                // show: true,
                fontSize: 10,
                color: '#e7e626ff',
                position: 'top'
              },
              lineStyle: {
                width: 1,
                type: 'solid'
              },
              areaStyle: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    { offset: 0, color: 'rgba(2, 251, 189, .2)' },
                    { offset: 1, color: 'rgba(2, 251, 189, 0)' }
                  ],
                  globalCoord: false
                }
              },
              data: this.echdata.yData
            }
          ]
        };
        echartEle.setOption(option);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
        //
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
