<template>
  <div :id="eleId" class="echart-unit"></div>
</template>

<script>
  import * as echarts from 'echarts';
  const defaultEchdata = {
    xData: ['2020年', '2021年', '2022年', '2023年', '2024年'],
    yData: [100, 220, 300, 450, 500]
  };
  const defaultSeriesName = ['系列1', '系列2'];
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: '标题'
      },
      seriesname: {
        type: [Object, Array],
        default: () => defaultSeriesName
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    watch: {
      echdata: {
        handler(newV, oldV) {
          this.initEchart();
        }
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        var imgArr = [
          'image://data:image/png;base64,',
          'image://data:image/png;base64,',
          'image://data:image/png;base64,',
          'image://data:image/png;base64,'
        ];
        const option = {
          backgroundColor: '#0e2147',
          grid: {
            left: '30',
            top: '5%',
            bottom: '5%',
            right: '5%'
          },
          legend: {
            type: 'scroll',
            data: '来电量',
            itemWidth: 18,
            itemHeight: 12,
            textStyle: {
              color: '#00ffff',
              fontSize: 14
            }
          },
          yAxis: [
            {
              type: 'value',
              position: 'left',
              nameTextStyle: {
                color: '#00FFFF'
              },
              splitLine: {
                lineStyle: {
                  type: 'dashed',
                  color: 'rgba(135,140,147,0.8)'
                }
              },
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                formatter: '{value}',
                color: '#fff',
                fontSize: 12
              }
            }
          ],
          xAxis: [
            {
              type: 'category',
              axisTick: {
                show: false
              },
              axisLine: {
                show: false,
                lineStyle: {
                  color: '#0696f9'
                }
              },
              axisLabel: {
                inside: false,
                textStyle: {
                  color: '#fff', // x轴颜色
                  fontWeight: 'normal',
                  fontSize: '14',
                  lineHeight: 22
                }
              },
              data: this.echdata.xData
            }
          ],
          series: [
            {
              //   symbolSize: 150,
              //   symbol: imgArr[2],
              name: '小灯光',
              type: 'line',
              itemStyle: {
                normal: {
                  borderWidth: 3,
                  color: '#0696f9'
                }
              },
              data: this.echdata.yData
            },
            {
              name: '滑行的光点',
              type: 'lines',
              coordinateSystem: 'cartesian2d',
              symbolSize: 30,
              polyline: true,
              effect: {
                show: true,
                trailLength: 0,
                // symbol: 'arrow',
                period: 10, //光点滑动速度
                symbolSize: 150,
                symbol: imgArr[0]
              },
              lineStyle: {
                normal: {
                  width: 1,
                  opacity: 0.6,
                  curveness: 0.2
                }
              }
              // data: data
            }
          ]
        };
        let echartEle = echarts.init(document.getElementById(this.eleId));
        echartEle.setOption(option);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
